<template>
  <div class="publish">
    <Panel>
      <template #header> 首页/发布 </template>
      <template #content>
        <el-form ref="form" :model="form" label-width="80px" :rules="rules">
          <el-form-item label="模块选择" prop="tab">
            <el-select v-model="form.tab" placeholder="请选择">
              <el-option label="分享" value="share"></el-option>
              <el-option label="问答" value="ask"></el-option>
              <el-option label="招聘" value="job"></el-option>
              <el-option label="客户端测试" value="dev"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="标题" prop="title">
            <el-input v-model="form.title"></el-input>
          </el-form-item>
          <el-form-item>
            <mavon-editor
              class="markdown"
              v-model="form.content"
              :subfield="false"
              :autofocus="false"
            />
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" @click="publish"
              >提交</el-button
            >
          </el-form-item>
        </el-form>
      </template>
    </Panel>
  </div>
</template>

<script>
import Panel from "../containers/Panel.vue";
import { mapActions, mapState } from "vuex";
export default {
  components: { Panel },
  async created() {
    console.log(this.id);

    this.form.tab = this.details.tab;
    this.form.title = this.details.title;
    this.form.content = this.details.content;
  },
  data() {
    return {
      form: {
        tab: "",
        title: "",
        content: "",
      },
      rules: {
        tab: { required: true, message: "请输入活动名称", trigger: "change" },
        title: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 6, max: 30, message: "长度在6 到 30个字符", trigger: "blur" },
        ],
      },
    };
  },
  computed: {
    ...mapState({
      details: (state) => state.details.value,
    }),
  },
  methods: {
    publish() {
      this.$refs.form.validate(async (valida) => {
        if (valida) {
          const token = window.localStorage.getItem("token");
          const res = await this.$axios.post("/topics", {
            accesstoken: token,
            ...this.form,
          });
          // console.log(res);
          this.$message.success("发布成功");
          this.$router.push({ name: "details", params: { id: res.topic_id } });
        }
      });
    },
    ...mapActions(["getDetails"]),
  },
};
</script>

<style lang="less">
.publish {
  .markdown {
    min-height: 500px;
  }
}
</style>
